/* Style du conteneur principal */
.account-container {
    max-width: 900px;
    margin: 40px auto;
    padding: 20px;
    background: #FBFBFB;
    border-radius: 8px;
    text-align: center;
    color: #010101;
}

.account-container h1{
    margin-bottom: 20px;
    font-size: 28px;
}

.account-container p,
.account-container form {
    margin-bottom: 10px;
}


body {
    min-height: 100vh;
}